import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import elBleeding from '../images/elbleeding.gif';
import '../styles/index.scss';

const IndexPage = () => (
  <Layout navNumber={0}>
    <SEO title="Home" />
    <h1>Hi people</h1>
    <p>Welcome to your new Gatsby site.</p>
    <p>Now go build something great.</p>
    <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
      <img src={elBleeding} alt="El" />
    </div>
    <Link to="/stranger-things-quiz/">Go to Quiz page</Link>
    <br />
    <Link to="/stranger-things-avatar-generator/">Go to Avatar Generator page</Link>
    <br />
    <Link to="/stranger-things-face-similarity/">Go to Face Similarity page</Link>
  </Layout>
)

export default IndexPage
